
.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
  
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;
  
    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;
  
    /* Support for IE. */
    font-feature-settings: 'liga';
  }

  .gallery{
      list-style: none;

      display: flex;
      flex-wrap: wrap;
      padding-left: 0;
      margin-top: 0;
    //   margin-right: -20px;
  }

  .gallery-item{
    
    flex-basis: calc((100% - 2 * 20px) / 2);
    margin-right: 20px;
    &:nth-child(2n + 2) {
      margin-right: 0;
    }
  }

  .stats{
      display: flex;  
  }

  .stats-item{

    &:not(:last-child){
        margin-right: 10px;
    }
  }

  .photo-card{
      display: inline-block;
      justify-content: center;

      margin-right: 10px;
  }

  .load-more-hide{
      display: none;
  }

  .load-more-visible{
    display: block;
    justify-content: center;
}